.main-variables {
  display: flex;
  flex-direction: column;

  .header-var,
  .list-variables {
    display: grid;
    grid-template-columns: repeat(2, 1fr) 2fr 0.5fr;
    width: 100%;
  }
  
  .container-variables {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .header-var {
    padding: 9px 0;
    &__item {
      font-family: "Inter SemiBold";
      color: #828292;
      font-size: 12px;
      font-weight: 500px;
    }
  }

  .list-variables{
    border-bottom: #f8f9ff 1px solid;
    font-size: 12px;
    color: #3e3f44;
    transition: all 0.3s;

    &__item{
      font-family: "Inter SemiBold";
      padding: 15px 0;
      display: flex;
      align-items: center;
      max-width: 217px;
      white-space: normal;
      word-break: break-word;
      &.options{
        display: flex;
        opacity: 0;
        justify-content: center;
        transition: all 0.3s;

       
      }

      .options{
        &__content{
          display: flex;
          align-items: center;
          justify-content: center;
          i{
            display: flex;
            font-size: 16px;
            padding: 2px;

          }
        }
      }
      
      &.type, &.description{
        color: #828292;
        font-weight: 500;
      }
    }
    &:hover{
      background: #f5f7ff;
      .options{
        opacity: 1;
        cursor: pointer;

        &__content{
          &:hover{
            color: $primary;
            border-radius: 35px;
            background: #fff;
            box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
          }
        }
      }
    }
  }
}


.option-variable-modal{
  &__item{
    padding: 12px 18px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 14px;
    font-family: "Inter SemiBold";
    cursor: pointer;

    svg{
      display: flex;
      margin-right: 14px;
    }

    &:hover{
      background: #f5f7ff;
    }

    &.del{
      color: #f7404e;
    }
  }
}