.docs {
  .header {
    padding: 0 32px;
  }
  &__main {
    height: calc(100vh - 60.99px);
    padding: 32px 32px;
    display: grid;
    grid-template-columns: 0.3fr 1fr 0.3fr;
    overflow-y: auto;

    &__col-left {
      &__item {
        transition: all 0.3s;
        cursor: pointer;

        .title {
          font-size: 16px;
          font-family: "Inter SemiBold";
          margin: 10px 0;
        }
        .container-topcs {
          height: 0;
          overflow: hidden;
          transition: all 0.3s;
          cursor: default;
        }

        &__topcs {
          display: inline-flex;
          align-items: flex-start;
          font-size: 14px;
          font-family: "Inter Regular";
          margin-bottom: 5px;
          color: #828292;
          position: relative;
          padding-left: 8px;
          cursor: pointer;

          &:before {
            content: "";
            padding: 0 0px;
            border-radius: 2px;
            height: 16px;
          }
          &.active {
            font-family: "Inter SemiBold";
            color: #131316;
            padding-left: 0;
            &:before {
              padding: 0 1px;
              margin-right: 7px;
              background: $primary;
            }
          }
        }

        &.active {
          .container-topcs {
            height: auto;
            
          }
        }
      }
    }
    &__col-middle {
      padding: 10px 70px;
      .title {
        font-size: 30px;
        font-family: "Inter SemiBold";
        margin-bottom: 6px;
      }
      article {
        border-bottom: 1px solid #e2e2ec;
        .text {
          line-height: 1.57;
          font-size: 14px;
        }
      }

      .rate {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
        .title {
          font-size: 20px;
          font-family: "Inter Bold";
        }
        &__icons {
          display: flex;
          margin-top: 10px;
          &__icon {
            margin-left: 33px;
            cursor: pointer;
            &:first-of-type {
              margin-left: 0;
            }

            svg {
              transition: all 0.3s;

              &:hover {
                transform: scale(1.2);
              }
            }
          }
        }
      }
    }
    &__col-right {
      .title {
        font-size: 16px;
        font-family: "Inter SemiBold";
      }

      &__items {
        &__item {
          margin: 10px 0;
          color: $primary;
          a:visited {
            color: $primary;
          }
        }
      }
    }
  }
}
