#getting-started {
  .modal {
    width: 581px;
  }

  .wrapper-content {
    display: none;

    .content-modes {
      display: flex;

      .container-mode {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-right: 19px;
        &:last-of-type {
          margin-right: 0;
        }


        .text{
          text-align: center;
          margin-top: 10px;
          font-size: 14px;
          font-weight: 400;
        }

        &.active {
          .content-modes__mode{
            border: 1px solid $primary;
          }

          .text{
            font-family: "Inter SemiBold";
          }
        }
      }

      &__mode {
        cursor: pointer;
        width: 100%;
        height: 160px;
        display: flex;
        border-radius: 4px;
        border: solid 1px #e2e2ec;

        img {
          width: 100%;
          align-self: flex-start;
        }

        &.light {
          background-color: #fff;
        }

        &.dark {
          background-color: black;
        }

        
      }
    }

    &.active {
      display: block;
    }
  }
  .modal__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    &__title {
      font-family: "Inter SemiBold";
      font-size: 24px;
    }
    &__number {
      font-family: "Inter SemiBold";
      font-size: 16px;
      color: $primary;
      width: 32px;
      height: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 16px;
      background: #f5f7ff;
    }
  }

  .modal__content {
    .title {
      font-family: "Inter SemiBold";
      font-size: 16px;
      margin-bottom: 15px;
      font-weight: 500;
    }

    label {
      .text {
        font-size: 14px;
        color: #828292;
      }
    }
    .radio-content input[type="radio"]:checked + label span {
      border: 1px solid $primary;
      .mask {
        background: $primary;
      }
    }
    .radio-content input[type="radio"]:checked + label {
      .text {
        font-family: "Inter SemiBold";
        font-size: 14px;
        color: #323232;
      }
    }
  }

  .modal__footer {
    margin-top: 20px;
    justify-content: space-between;
    display: flex;
    .modal__btn-primary {
      margin-right: 0;
    }
    .modal__btn-secondary {
      justify-content: flex-start;
      width: 97px;
    }
  }
  .wrapper-first-step {
    display: none;
    .main {
      &__getting-started {
        justify-content: center;
        &.developer {
          display: none;

          &.active {
            display: flex;
          }
        }
        &.manager {
          display: none;

          &.active {
            display: flex;
          }
        }
      }
    }

    &.active {
      display: flex;
    }
  }

  .wrapper-second-step {
    display: none;

    justify-content: center;
    .skill-coding {
      justify-content: center;

      &.basic {
        display: none;

        &.active {
          display: flex;
        }
      }
      &.intermediate {
        display: none;

        &.active {
          display: flex;
        }
      }
      &.advanced {
        display: none;

        &.active {
          display: flex;
        }
      }
    }

    &.active {
      display: block;
    }
  }

  .wrapper-third-step {
    .container-third-step {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 15px;

      &__item {
        width: 166px;
        // height: 115px;
        cursor: pointer;

        display: flex;
        flex-direction: column;
        align-items: center;
        transition: all 0.3s;
        .image-container {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 166px;
          height: 105px;
          border: 1px solid #d9dae1;
          border-radius: 4px;
          img {
            width: 55px;
            height: 55px;
          }
        }

        .title {
          font-family: "Inter Regular";
          font-size: 14px;
          margin-top: 11px;
          font-weight: 400;
          margin-bottom: 0;
        }

        &.active {
          .image-container {
            border: 1px solid $primary;
          }
          .title {
            font-family: "Inter SemiBold";
            font-size: 14px;
            color: #3e3f44;
          }
        }
      }
    }
  }
}
