.advanced-mode {
  display: none;

  &.active {
    display: block;
  }

  #call-agent {
    .constructor__container-start__input {
      padding-top: 17px;
    }

    .checkbox__list {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-top: 21px;
      font-size: 14px;
      font-weight: 600;

      &__item {
        display: flex;
        align-items: center;
        margin-bottom: 23px;
      }

      &__input {
        margin-right: 10px;
      }

      input[type="checkbox"] {
        border: solid 1px #bcbfd0;
      }
    }

    #client {
      border-bottom: none;
    }
  }

  #finish-block {
    .finish-container {
      border-bottom: none;
    }
    .constructor__container-start__input {
      input {
        font-size: 14px;
        color: #202126;
        border: dashed 1px #d9dae1;
        padding: 12px 22px;
      }
    }
  }

  #condition-block {
    .title-main {
      display: flex;
      color: #3e3f44;
      font-size: 16px;
      font-weight: 600;

      .number-title {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        border: solid 1px #d9dae1;
        width: 20px;
        height: 20px;
        font-size: 12px;
        font-weight: 600;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.33;
        margin-right: 5px;
      }
    }
    .condition-view {
      // height: 40px;
      width: 100%;
      padding: 12px 7px;
      margin: 10px 0 16px 0;
      border-radius: 4px;
      border: dashed 1px #d9dae1;
      font-weight: 600;
      font-size: 14px;
      &__content {
        display: flex;
        font-size: 12px;
        align-items: center;
        flex-wrap: wrap;
        white-space: pre-line;

        .var {
          margin: 0 4px;
          border-radius: 4px;
          border: solid 1px #e2e2ec;
          background-color: #fbfbff;
          font-weight: 400;
          padding: 0 2px;
        }
        .next-var {
          color: $primary;
          font-weight: 400;
          margin: 0 4px;
        }

        .param {
          color: #ffa75d;
          margin: 0 4px;
        }
      }
    }

    .define-condition-container {
      width: 100%;
      border-radius: 4px;
      border: solid 1px #e2e2ec;
      background-color: #fbfbff;
      padding: 14px 13px;

      .wrapper-conditionals {
        position: relative;
        border-radius: 4px;
        transition: all 0.3s;

        .list-drop--primary {
          left: 0;
          // top:139px;
          .list-drop__title {
            padding: 14px;
            margin: 0;
          }
          .list-drop__item {
            display: flex;
            justify-content: space-between;
            font-size: 14px;
          }
        }

        &.active {
          border: solid 1px #e2e2ec;
          padding: 12px;
        }

        &.hide {
          display: none;
        }
      }
      .buttom-var {
        display: flex;
        position: relative;
        background: #fff;
        color: #202126;
        border: solid 1px #bcbfd0;
        font-size: 14px;
        width: 100%;
        border-radius: 4px;
        padding: 8px;
        align-items: center;
        cursor: pointer;
        img {
          object-fit: cover;
          margin-right: 8px;
        }

        .var-list {
          .list-drop--primary {
            &.active {
              top: 42px;
            }
          }
        }
      }

      .var-list {
        display: flex;
        img {
          object-fit: cover;
          margin-right: 8px;
        }

        .list-drop--primary {
          .list-drop__title {
            padding: 14px;
            margin: 0;
          }

          left: 0;
          &.active {
            // top: 42px;
          }
        }
      }

      .container-conditionals {
        position: relative;

        .conditionals {
          display: flex;
          align-items: center;
          position: relative;
          font-size: 10px;
          color: #3e3f44;
          margin: 5px 0;
          transition: all 0.3s;

          img {
            width: 24px;
            height: 24px;
            margin-right: 8px;
          }

          .delete {
            position: absolute;
            right: 5px;
            width: 17px;
            height: 17px;
            object-fit: cover;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            opacity: 0;
            transition: all 0.3s;
            box-shadow: 0 2px 8px 0 rgba(32, 33, 38, 0.15);
            background-color: #fff;
            padding: 2px 3px;

            img {
              margin-top: 3px;
              align-self: center;
              margin-right: 0;
              margin-left: 3px;
            }
          }

          &:hover {
            background: #e9eaef;
            cursor: pointer;
            border-radius: 4px;
            .delete {
              opacity: 1;
            }
          }
        }
      }
    }

    .condition-input {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border: solid 1px #bcbfd0;
      margin-top: 0;
      // height: 40px;
      background: #fff;
      border-radius: 4px;
      input {
        border: none;
        width: 100%;
        height: 100%;
        padding: 10px 8px;
        border-radius: 4px;
      }
      img {
        cursor: pointer;
        padding: 8px;
      }
    }

    .conditional-operator {
      display: none;
      align-items: center;
      margin: 8px 0;
      img {
        margin-right: 8px;
      }
      &.active {
        display: flex;
      }
    }

    .add-if {
      margin: 21px 16px;

      button {
        font-family: "Inter Bold";
        border-radius: 5px;
        border: solid 1px #e2e2ec;
        background-color: #fbfbff;
        font-size: 14px;
        font-weight: bold;
        padding: 12px;
        cursor: pointer;
      }
    }
  }

  #location-block {
    .constructor__container-start__main {
      border-bottom: none;
    }
    .constructor__container-start__input {
      display: none;
      position: relative;
      margin-top: 17px;
      margin-bottom: 8px;

      .constructor__container-start__content__header__text {
        padding: 14px 14px 10px 14px;
      }

      input {
        padding-left: 41px;
      }
      img {
        position: absolute;
        left: 10px;
        top: 7px;
        cursor: pointer;
      }

      .btn--primary {
        display: none;
        margin-left: 18px;
        font-weight: 500;
        font-size: 14px;
        padding: 12px 10px;
        line-height: 10px;
        height: 35px;
        &.active {
          display: block;
        }
      }
      .list-drop {
        top: 40px;
        .list-drop__item {
          font-size: 14px;
          font-weight: 600;
          color: #3e3f44;
        }
      }
      &.active {
        display: flex;
      }
    }
    .location-container {
      display: none;

      .location-map {
        border-radius: 5px;
        height: 200px;
        border: #d9dae1 1px solid;
        margin: 8px 0;
      }

      .info {
        color: #3e3f44;
        letter-spacing: 0.2px;
        font-size: 12px;
        font-weight: 600;
      }

      &.active {
        display: block;
      }
    }

    .full-location {
      margin-top: 15px;
      display: none;
      &__inputs {
        display: flex;
        margin: 15px 0;
        input {
          font-family: "Inter Regular";
          color: #3e3f44;
          font-weight: 600;
          margin-right: 17px;
          width: 50%;
          border-radius: 5px;
          border: solid 1px #d9dae1;
          padding: 12px 14px;
          &:last-of-type {
            margin-right: 0px;
          }
        }
      }
      textarea {
        border-radius: 5px;
        border: solid 1px #d9dae1;
        width: 100%;
        height: 100px;
        resize: none;
        padding: 10px 14px;
        font-family: "Inter Regular";
        color: #3e3f44;
        font-weight: 600;
      }

      .checkbox {
        font-size: 14px;
        margin-top: 15px;
        color: #3e3f44;
        font-weight: 600;

        .checkbox_list_item {
          display: flex;
          align-items: center;
          input {
            margin-right: 15px;
          }
        }
      }

      &.active {
        display: block;
      }
    }
  }

  #start {
    .container-select {
      display: none;

      .list-drop {
        &__item {
          img {
            margin-right: 15px;
          }
        }
      }

      &.active {
        display: block;
      }
    }
  }

  #integrations {
    .constructor__container-start__main {
      &.active {
        // .constructor__content{
        //   display: ;
        // }
      }
    }
    .constructor__container-start__content:last-of-type {
      border-bottom: none;
    }

    .request {
      .select-bnt {
        text-transform: uppercase;
      }
      .container-select {
        max-width: 117px;

        .list-drop__item {
          display: flex;
          justify-content: space-between;
          letter-spacing: 0.23px;
          font-size: 14px;
          color: #3e3f44;

          img {
            display: none;
          }
          &.active {
            span {
              font-weight: 600;
            }
            img {
              display: block;
            }
          }
        }
      }
    }

    .wrapper-map {
      display: none;

      &.active {
        display: block;
      }

      .container-datasets {
        width: 100%;
        margin: 0;
        padding: 0;
        .title {
          font-family: "Inter Regular";
          font-weight: 400;
          display: flex;
          align-items: center;
          color: #3e3f44;
          font-size: 10px;
          cursor: pointer;

          img {
            width: 12px;
            height: 12px;
            object-fit: cover;
            margin-right: 4px;
          }
        }
        .dataset-item {
          color: #3e3f44;
          cursor: pointer;

          &.active {
            font-weight: 600;
          }

          ul {
            display: none;
          }

          &.show {
            .title {
              font-weight: 600;
            }
            ul {
              display: block;

              li {
                padding-left: 25px;
                padding-top: 4px;
                padding-bottom: 4px;
              }
            }
          }
          &.hide {
            .title {
              font-weight: 400;
              img {
                transform: rotate(-90deg);
              }
            }
            ul {
              display: none;
            }
          }

          // &:hover {
          //   background: #f5f7ff;
          // }
        }

        li {
          width: 100%;
          padding: 2px 5px;
          font-size: 10px;
          color: #3e3f44;
        }
      }
    }
  }
}

.simple-mode {
  display: none;
  overflow-y: auto;
  background: #f5f7ff;

  .constructor-simple {
    position: relative;
    // background: #f5f7ff;
    height: calc(100vh - 93px);
    display: grid;
    grid-template-columns: 1fr 366px 1fr;
  }

  .side-left {
    grid-column: 1;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
  }

  .side-right {
    grid-column: 3;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 30px 0;
    justify-content: center;
    align-items: center;
    position: relative;

    .btn-flow {
      position: absolute;
      bottom: 30px;
      right: 30px;
    }
  }

  .chat-creating {
    display: block;
    background: #fff;
    width: 366px;
    height: 100%;
    max-height: calc(100% - 40px);
    box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    border-radius: 5px;
    padding: 20px 10px;
    grid-column: 2;
    justify-self: center;
    align-self: center;
    overflow-y: auto;

    &__container-msg {
      display: flex;
      margin-top: 20px;
      flex-direction: column;

      .message {
        display: flex;
        width: 100%;
        max-width: 75%;
        margin: 5px 0 0 0;

        .wrapper-bubble {
          width: 100%;

          .message-bubble {
            padding: 10px;
            white-space: pre-line;
            word-break: break-all;
            border-radius: 10px;
            margin-bottom: 5px;
            font-size: 14px;

            .text {
              word-break: break-word;
              margin-bottom: 3px;
            }

            &--midia {
              width: 100%;
              display: flex;
              flex-direction: column;
              &__image {
                width: 100%;
                display: flex;
                img {
                  width: 100%;
                  object-fit: contain;
                  border-radius: 10px;
                }
              }
            }
          }
        }
      }
      .message-img {
        width: 25px;
        height: 25px;
        align-self: flex-end;
        img {
          width: 25px;
          height: 25px;
          object-fit: cover;
        }
      }

      &.left {
        justify-content: flex-start;

        .message {
          .message-img {
            margin-right: 10px;
          }

          &-bubble {
            border: 1px solid #d9dae1;
            white-space: pre-line;
            word-break: break-all;
            &:last-child {
              border-radius: 10px 10px 10px 0;
            }
          }
        }
      }

      &.right {
        align-items: flex-end;

        .message {
          flex-direction: row-reverse;
          .message-img {
            margin-left: 10px;
          }
          &-bubble {
            border: 1px solid #d9dae1;
            &:last-child {
              border-radius: 10px 10px 0px 10px;
            }
          }
        }
      }
    }

    &--wpp {
      background: url("../../build/images/background-wpp.jpg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;

      .right {
        .message-img {
          display: none;
        }
        .message-bubble {
          background-color: #dcf8c6;
        }
      }
      .left {
        .message {
          .message-img {
            display: none;
          }
          .message-bubble {
            background-color: #fff;
          }
        }
      }

      // &.active {
      //   display: block;
      // }
    }

    &--webchat {
      // &.active {
      //   display: block;
      // }
    }

    &--voice {
      // &.active {
      //   display: block;
      // }
    }
    &--messenger {
      .right {
        .message-bubble {
          background-color: #3578e5;
          color: #fff;
          border: 1px solid #3578e5;
        }
      }
      .left {
        .message-bubble {
          background-color: #f1f0f0;
          border: 1px solid #f1f0f0;
        }
      }
      // &.active {
      //   display: block;
      // }
    }
  }

  .container-actions {
    display: flex;

    .wrapper-actions {
      display: flex;
      flex-direction: column;
      margin-bottom: 60px;
    }

    .photo {
      align-self: flex-end;
      width: 42px;
      height: 42px;
      position: relative;

      z-index: 1;
      cursor: pointer;

      img {
        object-fit: cover;
        width: 100%;
      }
    }

    .bubble-tap {
      display: flex;
      align-items: center;
      padding: 8px 13px;
      font-size: 18px;
      border: dashed 1px #d9dae1;
      background: #fff;
      cursor: pointer;
    }

    .box-actions {
      display: none;

      width: 331px;
      border-radius: 8px;
      box-shadow: 0 25px 26px 1px rgba(22, 22, 32, 0.15);
      background: #fff;
      padding: 14px;
      flex-direction: column;
      transition: all 0.3s;
      z-index: 1;
      margin-top: 20px;

      &__content {
        width: 100%;
        margin-top: 10px;
        &:first-of-type {
          margin-top: 0;
        }

        &__midia-name {
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: 40px;
          padding: 12px;

          border-radius: 5px;
          border: dashed 1px #d9dae1;
          margin-bottom: 7px;
          margin-top: 10px;

          .text {
            color: #202126;
            font-size: 14px;
          }

          span {
            font-size: 16px;
            cursor: pointer;
          }
        }

        &__main {
          width: 100%;
          // background: pink;
          height: 108px;
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 5px;
          margin-bottom: 10px;
          margin-top: 10px;

          &__item {
            border-radius: 5px;
            width: 100%;
            height: 100%;

            img,
            video {
              object-fit: cover;
              width: 100%;
              height: 100%;
              border-radius: 5px;
            }
          }
        }

        .title {
          color: #828292;
          font-size: 12px;
          font-weight: 500;
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 10px;
          &:first-of-type {
            margin-top: 0;
          }

          svg {
            cursor: pointer;
            width: 18px;
            height: 18px;
          }
        }

        .container-message {
          border: solid 1px #828292;
          border-radius: 5px;
          width: 100%;
          margin-bottom: 15px;

          textarea {
            font-family: "Inter Regular";
            border-radius: 5px;
            padding: 6px 12px 0px 12px;
            border: none;
            width: 100%;
            resize: none;
            height: 80px;
          }

          &__emojis {
            border-top: 1px solid #f5f7ff;
            padding: 8px 12px;
            display: flex;
            position: relative;
            &__icon {
              display: flex;
              cursor: pointer;
              svg {
                width: 20px;
                height: 20px;
              }
            }
          }
        }

        // .select-btn {
        //   // margin-bottom: 10px;
        // }

        .conteiner-answer {
          display: flex;
          flex-direction: column;
          margin: 14px 0;
          &__input {
            display: flex;
            align-items: center;
            margin-bottom: 10px;

            &__num {
              font-family: "Inter SemiBold";
              font-size: 12px;
              width: 20px;
              height: 19px;
              border: 1px solid #d9dae1;
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 10px;
              margin-right: 9px;
              color: #828292;
            }

            input {
              border-radius: 4px;
              border: solid 1px #bcbfd0;
              padding: 8px 11px;
              width: 100%;
            }
          }

          .btn-add {
            font-family: "Inter SemiBold";
            margin-left: 26px;
            font-size: 14px;
            color: #828292;
            cursor: pointer;
            svg {
              margin-right: 8px;
              width: 20px;
              height: 20px;
            }

            &:hover {
              opacity: 0.9;
            }
            &:active {
              opacity: 1.1;
            }
          }
        }
      }

      .btn--primary {
        display: flex;
        align-self: flex-start;
        max-width: 105px !important;
        margin-top: 10px;
      }
    }

    &.left {
      .bubble-tap {
        border-radius: 10px 10px 10px 0px;
      }
      .box-actions {
        border-radius: 10px 10px 10px 0px;
      }

      .photo {
        align-self: flex-start;
        right: 40px;
      }

      &.active {
        .bubble-tap {
          display: none;
        }
        .box-actions {
          display: flex;
        }
      }
    }

    &.right {
      flex-direction: row-reverse;

      .photo {
        align-self: flex-end;
        left: 40px;
      }

      .bubble-tap {
        border-radius: 10px 10px 0px 10px;
      }

      .box-actions {
        border-radius: 10px 10px 0px 10px;

        .container-message {
          margin-bottom: 0;
        }

        .info {
          display: block;
          margin-bottom: 10px;
          font-size: 10px;
          &__enter {
            font-size: 12px;
          }
        }
      }

      &.active {
        .bubble-tap {
          display: none;
        }

        .box-actions {
          display: flex;
        }
      }
    }
  }

  &.active {
    display: block;
  }
}

.container-result {
  width: 100%;
  display: none;

  .result {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    border: dashed 1px #d9dae1;
    padding: 10px 14px;
    margin-top: 10px;

    &__content {
      display: flex;
      align-items: center;

      &__icon {
        margin-right: 7px;
        svg {
          display: flex;
          path {
            fill: #141b3f;
          }
        }
      }

      &__files {
        color: #202126;
      }
    }

    &__exclude {
      display: flex;
      align-items: center;
      cursor: pointer;
      svg {
        display: flex;
        path {
          fill: #828292;
        }
      }

      &:hover {
        opacity: 1.1;
      }
    }
  }

  &.active {
    display: block;
  }
}

.list-emoji {
  position: absolute;
  top: 35px;
  left: 0;
  display: none;
  flex-direction: column;
  padding: 9px 19px;
  box-shadow: 0 3px 10px 0px rgba(22, 22, 32, 0.15);
  margin-bottom: 17px;
  width: 100%;
  background-color: #fff;
  z-index: 3;
  border-radius: 2px;

  &__content-emoji {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 2px;
    width: 100%;
    padding: 1px;

    button {
      background: #fff;
      border: none;
      cursor: pointer;
      width: 22px;
      height: 22px;
    }
  }

  &__header {
    padding-bottom: 17px;
    margin-bottom: 8px;
    border-bottom: solid 1px #e2e2ec;
    .search__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
      margin-left: 7px;
    }
  }

  &__favorite {
    padding-bottom: 17px;
    margin-bottom: 8px;
    border-bottom: solid 1px #e2e2ec;
  }

  &__general {
    .list-emoji__content-emoji {
      max-height: 140px;
      overflow-y: auto;
    }
  }

  &.active {
    display: flex;
  }
}

.uploading-midia {
  display: none;
  flex-direction: column;
  background: rgba(245, 247, 255, 0.5);
  position: absolute;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  z-index: 2;

  &__progressbar-track {
    align-self: flex-start;
    height: 5px;
    width: 100%;
    border-radius: 5px 0px 0px 5px;
    transition: all 0.3s;
  }

  &__progressbar-bulet {
    border-radius: 5px 0px 0px 5px;
    background: $primary;
    height: 100%;
    opacity: 2;
  }
  .text {
    margin: auto;
    display: flex;
    align-items: center;
    padding: 11px 14px;
    background: #fff;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;

    span {
      cursor: pointer;
      font-size: 16px;
      margin-left: 2px;
    }
  }
  &.active {
    display: flex;
  }
}

.replace-midia {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background: transparent;
  z-index: 2;

  .text {
    margin: auto;
    display: flex;
    align-items: center;
    padding: 11px 14px;
    background: #fff;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;

    span {
      display: flex;
      cursor: pointer;
      font-size: 16px;
      margin-left: 2px;
      align-items: flex-start;
    }
  }

  &.active {
    display: flex;
  }
}
