.diagram {
  .header--sub-header {
    display: none;
  }

  .change-social-social {
    display: none;
  }

  .change-mode {
    display: none;
  }

  
  .search{
    display: flex;
    justify-self: center;
    width: 279px;
    background: #fff;

    &__btn {
      align-self: center;
      margin: 4px 0 0 0;
      cursor: pointer;
  }

    svg{
      margin: 0  0 0 10px;
    }
  }

  .pdf-export {
    display: flex;
    .pdf-button {
      display: flex;
      align-items: center;
      font-family: "Inter SemiBold";
      color: $primary;
      cursor: pointer;
      background: #edeffa;
      border:none;
      border-radius: 5px;
      padding: 10px 14px;

      svg{
        width: 20px;
        height: 20px;
        margin-right: 8px;
        path{
          fill: $primary;
        }
      }
    }
  }

  .main-diagram{
    position: relative;
    width: 100%;
    height:calc(100vh - 60px);
    display: flex;
    justify-content: center;

    .btn-flow{
      position: fixed;
      bottom: 30px;
      right: 30px;
      
      span{
        margin-right: 4px;
      }
    }
  }
}
