#add-parameters {
  .modal__container {
    padding: 44px 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: visible;

    .header-modal {
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        color: $primary;
        font-size: 25px;
        margin-bottom: 8.5px;
      }
      .text {
        color: #bcbfd0;
        font-size: 16px;
      }
    }

    .modal__content {
      max-width: 287px;
      padding: 40px 0;
      .container-list {
        position: relative;
        display: flex;
        justify-content: center;
        flex-direction: row;

        .select-btn {
          display: flex;
          border: 1px solid #bcbfd0;
          border-radius: 5px;
          align-items: center;
          justify-content: space-between;
          padding: 10px 15px;
          font-size: 16px;
          height: 40px;
          width: 287px;
          cursor: pointer;
          .text {
            padding-left: 25px;
          }

          .icon {
            display: flex;
            align-items: center;
            cursor: pointer;
          }
        }

        .list-parameters {
          padding: 0;
          display: none;
          position: absolute;
          background: #fff;
          width: 100%;
          top: 26px;
          z-index: 2;
          box-shadow: 0px 4px 10px #00000027;
          border-radius: 5px;
          min-height: 40px;
          max-height: 320px;
          overflow-y: auto;

          &__item {
            padding-left: 40px;
            padding-bottom: 10px;
            font-size: 15px;


            cursor: pointer;
            &:first-of-type{
                padding-top: 10px;
                // margin-bottom: 0;
            }
            &:hover{
                font-weight: 600;
            }
          }
          &.active{
            display: block;
          }
        }
      }
    }
  }
}
